<template>
  <div class="main" ref="scrollWrap">
    <section class="scroll-section" id="scroll-section-0" ref="scrollSection0" :style="`background-image: url(${이미지})`">
      <div class="hero">
        <div class="title">Leag of Legends</div>
        <div></div>
        <div class="sticky-elem hero-message a" ref="message1">
          <div class="year">10th ANNIVERSARY</div>
        </div>
        <!-- <div class="sticky-elem hero-message b" ref="message2">
          <div class="thanks">THANK YOU SUMMONERS</div>
        </div>-->
        <div class="sticky-elem hero-message c" ref="message3">
          <div class="greeting">리그오브레전드와 10년간 함께 해주신 소환사 여러분, 감사합니다!</div>
          <div class="greeting">THANK YOU, SUMMONERS!</div>
        </div>
      </div>
    </section>
   <section class="scroll-section" id="scroll-section-1" ref="scrollSection1">
     <p>
       Lorem, ipsum dolor sit amet consectetur adipisicing elit. Assumenda<br>
       Lorem, ipsum dolor sit amet consectetur adipisicing elit. Assumenda<br>
     </p>
   </section>
    <section class="scroll-section" id="scroll-section-2" ref="scrollSection2">
     <p>
       Lorem, ipsum dolor sit amet consectetur adipisicing elit. Assumenda<br>
       Lorem, ipsum dolor sit amet consectetur adipisicing elit. Assumenda<br>
     </p>
   </section>
    <section class="evt">
      <div class="evt-title">이벤트 대상자인지 확인해보세요!</div>
      <div>
        <img class="evt-pengu" v-if="penguIs" v-bind:src="penguFeel?pengu:penguHeart">
      </div>
      <div class="evt-content">
        <div class="evt-form">
          <div class="evt-input-box">
            <input class="evt-input" type="text" @change="input" placeholder="소환사 닉네임 입력" />
          </div>
          <button class="evt-btn" @click="click(), fetchData()">검색</button>
        </div>

        <div>
          <div v-if="isVal">
            <p>{{playerName}}소환사님의 레벨은 {{playerLevel}}!</p>
            <p>티어는 {{playerTier}} 입니다</p>
            <p v-if="penguFeel">이벤트 대상자가 아닙니다</p>
            <p v-else>축하합니다! 이벤트 대상자입니다</p>
          </div>

          <div v-if="isNan">
           <img :src="require('@/assets/imgs/sad.png')">
           <div>{{errorText}}</div>
          </div>
        </div>

        <section v-if="!penguFeel">
        <transition
          appear
          @before-enter="beforeEnter"
          @enter="enter"
          @afterEnter="afterEnter">
            <div class="cards">
              <div v-for="card in cards" :key="card.id" class="card" @click="showModal = true"></div>
            </div>
          </transition>
        </section>
      </div>
      <transition name="fade" appear>
                <ModalView v-if="showModal ==  true"  :showModal="showModal" @closeModal="closeModal"></ModalView>
      </transition>

      <router-view></router-view>
      <router-link to="/JH">링크</router-link>
    </section>
  </div>

</template>

<script>
  import { onMounted, ref} from 'vue'
  import axios from 'axios'
  import { gsap } from "gsap";
  import ModalView from './components/ModalView.vue'

  //씬을나눈다
  //씬의 높이를 세팅한다
  //활성화 시킬 씬을 결정한다
  export default {
    name: 'App',
    components: {
     ModalView:ModalView,
    },
    data() {
      return {
        /* main */
        showModal: false,
        isVal : false,
        isNan : false,
        errorText: "존재하지않는 소환사입니다",
        이미지: "https://universe.leagueoflegends.com/images/championsBackground.jpg",
        pengu : require('@/assets/imgs/pengu.png'),
        penguHeart : require('@/assets/imgs/heart.png'),
        penguIs: true,
        penguFeel : true,
        /* axios */
        summonInfo: [],
        playerID: '',
        playerName: '',
        playerLevel: '',
        playerTier: '',
        unrankMessage: "언랭크",
        apiKey : 'RGAPI-e0221e85-ec58-4234-bf04-682d1cfcc293',
        text: '', //출력될 값
        inputText:'', //input값
        cards: [
          { id: 1298 },
          { id: 8748 },
          { id: 4919 },
        ],
        /* scroll animation */
        sceneInfo: [
          {
            type: 'sticky',
            heightNum: 4,
            scrollHeight: 0,
            objs: {
              // container: document.querySelector('scroll-section-0')
              container : this.$refs.scrollSection0,
              messageA : this.$refs.message1,
              messageB : this.$refs.message2,
              messageC : this.$refs.message3,
            },
            values: {
              messageA_opacity_in: [0,1, {start: 0.1, end: 0.2}],
              messageA_opacity_out: [1,0, {start: 0.25, end: 0.3}],
              messageA_translateY_in: [20, 0, {start: 0.1, end: 0.2}],
              messageA_translateY_out: [0, -20, {start: 0.25, end: 0.3}],
              messageB_opacity_in: [0,1, {start: 0.3, end: 0.4}],
              messageB_opacity_out: [0,1, {start: 0.45, end: 0.5}],
            }
          },
          {
            type: 'normal',
            heightNum: 4,
            scrollHeight: 0,
            objs: {
               container: this.$refs.scrollSection1
            }
          },
          {
            type: 'sticky',
            heightNum: 4,
            scrollHeight: 0,
            objs: {
               container: this.$refs.scrollSection2
            }
          },
        ],
        //window.pageYOffset 갱신
        yOffset : 0,
        //현재 스크롤 위치(yOffset)보다 이전에 위치한 스크롤 섹션들의 스크롤 높이값의 합
        prevScrollHeight : 0,
        //현재 활성화된 scene
        currentScene : 0,
        totalScrollHeight: 0,
        messageA_opacity_in: '',
        messageA_opacity_out: '',
        messageA_translateY_in: '',
        messageA_translateY_out:'',
        messageB_opacity_in: '',
        messageB_opacity_out: '',
        currentYOffset: 0,
        rv: 0,
        values: '',
        scrollRatio: 0,
        outerScrollRatio:0,
        enterNewScene: false,
        scrollHeight: 0,
        partSCrollStart: 0,
        partScrollEnd: 0,
        partScrollHeight: 0,
        currentSceneValues: '',
        infoCurretScene: 0,
        allValues0 : [],
        allValues1 : [],
        allValues2 : [],
      }
    },
    setup(){
      const beforeEnter = (el) =>{
        console.log('before enter - set initial state');
        el.style.transform = 'translateY(-120px)'
        el.style.opacity = 0
      }
      const enter = (el,done) =>{
        console.log('starting to enter - make transition');

        //애니메이트할 대상, 오브젝트
        gsap.to(el,{
          duration: 3,
          y:0,
          opacity: 1,
          ease: 'bounce.out',
          onComplete: done
        });
      }
      const afterEnter = () => {
        console.log('after enter');
      }

      return{
        beforeEnter, enter, afterEnter
      }
    },

    methods: {
        input(e){
          return this.inputText = e.target.value
          //input에 입력된 값 inputText로 넣어주기

        },
        click(){
          return this.text = this.inputText

          //출력됡밧인 text에 input값을 넣어준 inputText를 넣어주기
        },
        closeModal(){
          this.showModal = false;
        },
        fetchData: function () {
          axios.get(`https://kr.api.riotgames.com/lol/summoner/v4/summoners/by-name/${this.text}?api_key=${this.apiKey}`)
          .then((res1) => {
            this.summonInfo.push(res1.data);
            this.playerID = (res1.data.id);
            this.playerName = (res1.data.name);
            this.playerLevel = (res1.data.summonerLevel);
            this.isVal = true;
            this.isNan = false;
            this.penguIs = true;

              axios.get(`https://kr.api.riotgames.com/lol/league/v4/entries/by-summoner/${this.playerID}?api_key=${this.apiKey}`)
                .then((res2) => {

                  if(res2.data[0] === undefined){
                    this.playerTier = this.unrankMessage;
                    this.penguFeel = true;
                  }else {
                    this.playerTier = (res2.data[0].tier);
                      if(this.playerTier == 'GOLD'){
                        this.penguFeel = false;
                           console.log(this.res2.data[0].tier);
                      }else {
                        this.penguFeel = true;
                      }
                  }

                }).catch( error => {
                   console.log("에러입니다" + error);
                });

          }).catch( error => {
               if (error.status == undefined) {
                   this.isNan = true;
                   this.isVal = false;
                   this.penguIs = false;
               }
          });
        },
        isValdata(){
          this.isVal == true;
        },
        setLayout() {
         //재할당
          this.sceneInfo[0].objs.container = this.$refs.scrollSection0;
          this.sceneInfo[1].objs.container = this.$refs.scrollSection1;
          this.sceneInfo[2].objs.container = this.$refs.scrollSection2;

          //각 section의 높이값 설정
          for (let i = 0; i < this.sceneInfo.length; i++) {
            this.sceneInfo[i].scrollHeight = this.sceneInfo[i].heightNum * window.innerHeight;
            this.sceneInfo[i].objs.container.style.height = `${this.sceneInfo[i].scrollHeight}px`
          }

          this.yOffset = window.pageYOffset;
          for ( let i=0; i<this.sceneInfo.length; i++){
            this.totalScrollHeight += this.sceneInfo[i].scrollHeight;
            if( this.totalScrollHeight >= this.pageYOffset){
              this.currentScene = i;
              break;
            }
          }
          this.$refs.scrollWrap.setAttribute('id', `show-scene-${this.currentScene}`);
        },

        scrollLoop() {
          //yoffset값이 이전 씬의 값들합볻다 크면 다음 씬이 진행중
          //활성화 시킬 씬이 무엇이냐?
          this.yOffset = window.pageYOffset;
          // this.sceneInfo[0].scrollHeight


          this.prevScrollHeight = 0;//초기화

          for (let i = 0; i < this.currentScene; i++) {
            this.prevScrollHeight =  this.prevScrollHeight+this.sceneInfo[i].scrollHeight;
          }

          if( this.yOffset > this.prevScrollHeight + this.sceneInfo[this.currentScene].scrollHeight) {
            this.enterNewScene = true;
            this.currentScene++;
            this.$refs.scrollWrap.setAttribute('id', `show-scene-${this.currentScene}`);
          }

          if(this.yOffset <this.prevScrollHeight) {
            this.enterNewScene = true;
            if(this.currentScene === 0) return;
            this.currentScene--;
            this.$refs.scrollWrap.setAttribute('id', `show-scene-${this.currentScene}`);
          }

          if(this.enterNewScene) return;

          this.playAnimation();

        },
        playAnimation(){
        // const values = this.sceneInfo[this.currentScene].objs;
        // const values = this.sceneInfo[this.currentScene].values;
        this.currentYOffset = this.yOffset - this.prevScrollHeight;


         switch (this.currentScene) {
          case 0:

          this.messageA_opacity_in = this.calcValues(this.sceneInfo[0].values.messageA_opacity_in,  this.currentYOffset);
          this.messageA_opacity_out = this.calcValues2(this.sceneInfo[0].values.messageA_opacity_out,  this.currentYOffset);
  
          // this.messageA_translateY_in = `translate3d(0, ${this.calcValues(this.sceneInfo[0].messageA_translateY_in, this.currentYOffset)}%, 0)`;
          // this.messageA_translateY_out = `translate3d(0, ${this.calcValues2(this.sceneInfo[0].messageA_translateY_out, this.currentYOffset)}%, 0)`;


          if(this.outerScrollRatio <= 0.22){
            //in
            this.$refs.message1.style.opacity = this.messageA_opacity_in;

          } else {
            //out
            this.$refs.message1.style.opacity = this.messageA_opacity_out;
          }

          if (this.scrollRatio <= 0.42) {
            this.$refs.message3.style.opacity = this.messageB_opacity_in;
          }else{
            this.$refs.message3.style.opacity = this.messageB_opacity_out;
          }

          break;

          case 1:

          break;
         }
       },

        calcValues(values, currentYOffset) {
          this.rv;

          this.scrollHeight = this.sceneInfo[this.currentScene].scrollHeight;
          //현재 scene에서 스크롤된 범위를 비율로 구하기
          this.scrollRatio = this.currentYOffset / this.scrollHeight;
          this.outerScrollRatio = (this.yOffset - this.prevScrollHeight) / this.scrollHeight;
          this.infoCurretScene = this.sceneInfo[this.currentScene];
          this.currentSceneValues = this.sceneInfo[this.currentScene].values;

          if (this.currentSceneValues.messageA_opacity_in.length === 3) {

            //start ~ end 사이에 애니메이션 실행
            this.partSCrollStart = this.currentSceneValues.messageA_opacity_in[2].start * this.scrollHeight;
            this.partScrollEnd = this.currentSceneValues.messageA_opacity_in[2].end * this.scrollHeight;
            this.partScrollHeight = this.partScrollEnd - this.partSCrollStart;

            if (this.currentYOffset >= this.partSCrollStart && this.currentYOffset <= this.partScrollEnd) {
              this.rv = (this.currentYOffset - this.partSCrollStart) / this.partScrollHeight * (this.currentSceneValues.messageA_opacity_in[1] - this.currentSceneValues.messageA_opacity_in[0]) + this.currentSceneValues.messageA_opacity_in[0];
            } else if (this.currentYOffset < this.partSCrollStart) {
              this.rv = this.currentSceneValues.messageA_opacity_in[0];
            } else if (this.currentYOffset > this.partScrollEnd) {
              this.rv = this.currentSceneValues.messageA_opacity_in[1];
            }

          } else {
            this.rv = this.scrollRatio * (this.currentSceneValues.messageA_opacity_in[1] - this.currentSceneValues.messageA_opacity_in[0]) + this.currentSceneValues.messageA_opacity_in[0];
          }

          return this.rv;
        },

        calcValues2(values, currentYOffset) {
          this.rv;
          this.scrollHeight = this.sceneInfo[this.currentScene].scrollHeight;
          //현재 scene에서 스크롤된 범위를 비율로 구하기
          this.scrollRatio = this.currentYOffset / this.scrollHeight;
          this.outerScrollRatio = (this.yOffset - this.prevScrollHeight) / this.scrollHeight;
          this.infoCurretScene = this.sceneInfo[this.currentScene];
          this.currentSceneValues = this.sceneInfo[this.currentScene].values;

          if (this.currentSceneValues.messageA_opacity_out.length === 3) {
            //start ~ end 사이에 애니메이션 실행
            this.partSCrollStart = this.currentSceneValues.messageA_opacity_out[2].start * this.scrollHeight;
            this.partScrollEnd = this.currentSceneValues.messageA_opacity_out[2].end * this.scrollHeight;
            this.partScrollHeight = this.partScrollEnd - this.partSCrollStart;

            if (this.currentYOffset >= this.partSCrollStart && this.currentYOffset <= this.partScrollEnd) {
              this.rv = (this.currentYOffset - this.partSCrollStart) / this.partScrollHeight * (this.currentSceneValues.messageA_opacity_out[1] - this.currentSceneValues.messageA_opacity_out[0]) + this.currentSceneValues.messageA_opacity_out[0];
            } else if (this.currentYOffset < this.partSCrollStart) {
              this.rv = this.currentSceneValues.messageA_opacity_out[0];
            } else if (this.currentYOffset > this.partScrollEnd) {
              this.rv = this.currentSceneValues.messageA_opacity_out[1];
            }
          } else {
            this.rv = this.scrollRatio * (this.currentSceneValues.messageA_opacity_out[1] - this.currentSceneValues.messageA_opacity_out[0]) + this.currentSceneValues.messageA_opacity_out[0];
          }

          return this.rv;
        },
      },
      mounted() {
        window.addEventListener('DOMContentLoaded', function(event){
          this.setLayout;
        });

        window.addEventListener('resize', this.setLayout());
        window.addEventListener('scroll', () => {

          //스크롤이 일어날 때 yOffset 의 값을 window.pageYOffset 값으로 갱신
          this.scrollLoop();
        });

      },
  }
</script>

<style lang="scss">
  body {
    margin: 0;
  }

  .scroll-section {
    border: 1px solid red
  }
</style>