<template>
  <div>
    <transition name="fade" appear>
      <div class="modal-overlay" v-if="showModal" @click="closeModal">
      </div>
    </transition>
    <transition name="slide" appear>
      <div class="modal" v-if="showModal">
        <div>
          안녕<br>
          안녕<br>
          안녕
          <div ref="box" class="greenbox">
            
          </div>
        </div>
        <div>
          <button class="btn-close" @click="closeModal">Close Modal</button>
        </div>
      </div>
    </transition>
  </div>
</template>
<script>
  import { onMounted, ref} from 'vue'
  import {gsap} from "gsap";

  export default {
    name: 'modal',
    props: {
      showModal : Boolean,
    },
    setup() {
      const box = ref();
      onMounted(() => {
        gsap.to(box.value, { rotation: "+=360" });
      });

      return {
        box,
      };
    },
    methods: {
      closeModal(){
        this.$emit('closeModal');
      }
    },
  }
</script>
<style lang="scss">
  .greenbox {
    width: 60px;
    height: 60px;
    background-color: red;
  }
</style>